<!-- 科室管理 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :lg="4" :md="8">
        <Input v-model="divisionName" placeholder="科室名称" class="input-width"></Input>
      </Col>
      <Col :lg="4" :md="8">
        <Button type="success">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="success" @click="mgrModal = true">
        <Icon type="md-add" />添加
      </Button>
      <Button type="error" class="margin-left">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Table :columns="column" :data="data"></Table>
    <br />
    <Page :total="100" />

    <!-- 对话框 -->
    <Modal v-model="mgrModal" title="科室管理">
      <Form ref="formItem" :model="formItem">
        <FormItem label="所属部门">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="科室编号">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="科室名称">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="所在楼层">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
        <FormItem label="绑定卡号">
          <Input v-model="formItem.no" clearable></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="mgrModal = false">关闭</Button>
        <Button type="success">保存</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "DivisionMgr",
  data() {
    return {
      divisionName: "",
      mgrModal: false,
      formItem: {
        no: ""
      },
      column: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          key: "no",
          title: "科室编号",
        },
        {
          title: "科室名称",
        },
        {
          title: "所在楼层",
        },
        {
          title: "所属部门",
        },
        {
          title: "修改",
          width: 100,
          render: h => {
            return h(
              "a",
              {
                on: {
                  click: () => {
                    this.updateModal = true;
                  }
                }
              },
              [
                h("Icon", {
                  props: {
                    type: "md-create",
                    size: 24
                  }
                })
              ]
            );
          }
        }
      ],
      data: [{ no: "aaa" }]
    };
  },
  methods: {}
};
</script>
 